<template>
	<div class="search">
		<label class="search-label">{{label}}</label>
		<input class="search-input" v-model="seValue" placeholder="检索..." />
		<button class="btn-success" @click="submi">
			<svg class="icon" aria-hidden="true">
				<use xlink:href="#icon-Search"></use>
			</svg>
		</button>
	</div>
</template>

<script>
	export default {
		name: 'Search',
		data: function() {
			return {
				seValue: '', //搜索框内容
			}
		},
		methods:{
			submi:function(){  //点击按钮提交搜索框内容给父组件
				this.$emit('searFunc',this.seValue)  
			}
		},
		props: ['label']
	}
</script>

<style scoped>
	.search{
		display: flex;
		align-items: center;
	}
	
	.search-label {
		border: solid 1px #ccc;
		border-right: none;
		padding: 7.5px 15px;
		border-radius: 2px;
		color: #555;
	}

	.search-input {
		height: 36px;
		padding: 6px 15px;
		font-size: 14px;
		line-height: 1.57142857;
		color: #555;
		background-color: #fff;
		background-image: none;
		border: 1px solid #ccc;
		border-radius: 2px;
		transition: box-shadow .25s linear,border .25s linear,color .25s linear,background-color .25s linear;
	}
	.search-input:focus{
		border-color: #3f51b5;
		box-shadow: none;
	}
	.btn-success {
		height: 36px;
		width: 60px;
		color: #fff;
		background-color: #5cb85c;
		border-color: #4cae4c;
		cursor: pointer;
	}
	.btn-success:hover{
		background-color: #4caf50;
		border-color: #4caf50;
	}
</style>
