<template>
	<div class="custom-tree-container">
		<div class="block">
			<p>产品分类</p>
			<!-- data:展示数据
				show-checkbox: 节点是否可被选择
				node-key:每个数节点用来作为唯一标识的属性，整颗树应该是唯一的 
				highlight-current:是否高亮当前选中节点 
				expand-on-click-node:是否在点击节点的时候展开或者收缩节点，默认值为true，如果为false
				则只有点箭头图标的时候才会展开或者收缩节点	-->
			<el-tree :data="listData" node-key="id" highlight-current :expand-on-click-node="false">
				<!-- 使用scoped slot会传入两个参数node 和 data
					分别表示当前节点的node对象和当前节点的数据	-->
				<span class="custom-tree-node" slot-scope="{ node, data }">
					<!-- 分级标题 -->
					<span>{{ node.label }}</span>
					<span>
						<svg class="icon" aria-hidden="true" @click="() => open(data)">
							<use xlink:href="#icon-icon-test"></use>
						</svg>
						<svg class="icon" aria-hidden="true" @click="() => getCurrentNode(data)">
							<use xlink:href="#icon-edit-1-copy"></use>
						</svg>
						<svg class="icon" aria-hidden="true" @click="() => remove(node, data)">
							<use xlink:href="#icon-guanbi"></use>
						</svg>
					</span>
				</span>
			</el-tree>
		</div>
	</div>
</template>

<script>
	let id = 1000;

	export default {
		data() {
			return {
				listData: [{
					id: 1,
					label: '一级 1',
					children: [{
						id: 4,
						label: '二级 1-1',
						children: [{
							id: 9,
							label: '三级 1-1-1'
						}, {
							id: 10,
							label: '三级 1-1-2'
						}]
					}]
				}, {
					id: 2,
					label: '一级 2',
					children: [{
						id: 5,
						label: '二级 2-1'
					}, {
						id: 6,
						label: '二级 2-2'
					}]
				}, {
					id: 3,
					label: '一级 3',
					children: [{
						id: 7,
						label: '二级 3-1'
					}, {
						id: 8,
						label: '二级 3-2'
					}]
				}]
			}
		},

		methods: {
			open(data) {
				this.$prompt('请输入分类名称', '创建产品分类', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
				}).then((res) => {
					this.append(data, res.value)
					this.$message({
						type: 'success',
						message: '添加成功 '
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '取消添加'
					});
				});

			},
			append(data, label) {
				const newChild = {
					id: id++,
					label: label,
					children: []
				};
				if (!data.children) {
					this.$set(data, 'children', []);
				}
				data.children.push(newChild);
			},
			getCurrentNode(data) {
				console.log(data)
				this.setLabel(data.id,this.listData)
			},
			setLabel(id, data) {
				
				for (let i = 0; i < data.length; i++) {
					if (data.id === id) {
						console.log(data)
					} else {
						if(data.children){
							this.setLabel(id, data.children)
						}
					}
				}
			},
			remove(node, data) {
				const parent = node.parent;
				const children = parent.data.children || parent.data;
				const index = children.findIndex(d => d.id === data.id);
				children.splice(index, 1);
			},
		}
	};
</script>

<style scoped>
	.custom-tree-node {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;
		padding-right: 8px;
	}

	.icon {
		display: none;
		color: #9e9e9e;
	}
	.el-tree-node__content:hover .icon{
		display: inline-block;
	}
</style>
